
.fade-enter-active {
  -webkit-transition: all .4s ease-in-out;
}
.fade-leave-active {
  -webkit-transition: all .7s ease;
}
.fade-enter, .fade-leave-active {
  /*-webkit-transform: translateY(100px);*/
  -webkit-transform: scale(0.5);
  opacity: 0;
}



